.courses-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 100px 20px;
}

@font-face {
    font-family: 'Rimma Sans';
    src: url('/fonts/RIMMA_SANS-BOLD.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Involve';
    src: url('/fonts/Involve-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}


.courses-title {
    font-size: 36px;
    font-family: 'Rimma Sans';
    font-weight: bold;
    margin-bottom: 30px;
}

.courses-content {
    display: flex;
    justify-content: space-around;
    width: 100%;
    max-width: 1200px;
}

.course-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 45%;
}

.course-link {
    text-decoration: none;
    width: 100%;
}

.course-box {
    width: 100%;
    height: 300px; /* Увеличенный размер */
    background-color: #1C199A;
    border-radius: 20px;
    font-family: 'Involve';
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 18px;
    padding: 20px;
    box-sizing: border-box;
    text-align: center;
}

.course-button {
    margin-top: 50px;
    font-family: 'Involve';
    background-color: #BEDB45;
    border: none;
    padding: 20px 40px;
    border-radius: 20px;
    font-size: 16px;
    font-weight: bold;
    color: #000;
    cursor: pointer;
    transition: background-color 0.3s;
}

.course-button:hover {
    background-color: #b1c700;
}
